.top .logo {
  height: 0.9em;
}

.task-text {
  padding-left: 20px
}

.todo-item .checkbox label:before {
  content: "x";
  border-radius: 3px;
}

.todo-item .delete-task-btn button {
  margin: 2px;
  padding: 0 1.2em;
}

.todo-item .delete-task-btn button:hover {
  -webkit-transform: scale(1.4);
          transform: scale(1.4)
}

.todo-item .delete-task-btn button::after {
  background: transparent
}

input[type=checkbox]:checked {
  content: "[done!]";
  text-shadow: 1px 1px 1px rgba(0, 0, 0, .2);
  font-size: 15px;
  color: #f3f3f3;
  text-align: center;
  line-height: 15px;
}

.todo-item {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.tasks-enter {
  -webkit-transform: translateX(-120%) rotateY(360deg);
  transform: translateX(-120%) rotateY(360deg);
  -webkit-transition: 0.5s;
  transition: 0.5s;
  max-height: 0;
  padding: 0 !important;
}

.tasks-enter.tasks-enter-active {
  max-height: 60px;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  padding: 2rem 0 !important;
}

.tasks-exit {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

.tasks-exit.tasks-exit-active {
  -webkit-transform: translateX(120%);
  transform: translateX(120%);
  padding: 0;
}

