button, input[type=submit] {
  text-transform: uppercase;
  background: none;
  border: 1px solid #000;
  font-weight: 600;
  font-size: 1.5rem;
  font-family: 'Open Sans';
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  position: relative;
  z-index: 2;
}

button[disabled], input[type=submit][disabled] {
  color: #d12028;
  background: #fff;
  border-color: #d12028;
  -webkit-transform: rotate(-10deg) scale(2) translateX(50%) translateY(-50%);
  transform: rotate(-10deg) scale(2) translateX(50%) translateY(-50%);
}

button[disabled]:hover, input[type=submit][disabled]:hover {
  color: #d12028;
  cursor: not-allowed;
}

button[disabled]:after, input[type=submit][disabled]:after {
  display: none;
}

button:after, input[type=submit]:after {
  content: '';
  z-index: -1;
  display: block;
  background: #000;
  position: absolute;
  width: 100%;
  height: 0;
  left: 0;
  top: 0;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

button:hover, input[type=submit]:hover, button:focus, input[type=submit]:focus {
  color: #fff;
  outline: 0;
}

button:hover:after, input[type=submit]:hover:after, button:focus:after, input[type=submit]:focus:after {
  height: 100%;
}

button.warning:after, input[type=submit].warning:after {
  background: #d12028;
}

button.success:after, input[type=submit].success:after {
  background: #2dc22d;
}

button.github, input[type=submit].github, button.facebook, input[type=submit].facebook, button.twitter, input[type=submit].twitter {
  border: 0;
  display: block;
  margin-bottom: 2rem;
  width: 100%;
  color: #fff;
  padding: 2rem;
}

button.github, input[type=submit].github {
  background: #82d465;
}

button.github:after, input[type=submit].github:after {
  background: #5cc437;
}

button.facebook, input[type=submit].facebook {
  background: #3864a3;
}

button.facebook:after, input[type=submit].facebook:after {
  background: #2d5082;
}

button.twitter, input[type=submit].twitter {
  background: #5ea9dd;
}

button.twitter:after, input[type=submit].twitter:after {
  background: #2c8dd0;
}

